import axios from 'axios'
import urlConfig from './config'
import qs from 'qs'

const wwwService = axios.create({
  timeout: 8000,
  transformRequest: [function (data) {
    data = qs.stringify(data)
    return data
  }],
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
  }
})

wwwService.interceptors.request.use(function (config) {
  return config
}, function (error) {
  return Promise.reject(error)
})


export default {

  fetchData () {
    return axios({
      method: 'get',
      url: 'http://pc2b39ydf.bkt.clouddn.com/data.json',
      headers: {
        'Content-Type': 'application/json;charset=utf-8'
      }
    })
  },
  fetchLogicData () {
    return axios({
      method: 'get',
      url: 'http://pc2b39ydf.bkt.clouddn.com/logic.json',
      headers: {
        'Content-Type': 'application/json;charset=utf-8'
      }
    })
  },
  fetchVideoData () {
    return axios({
      method: 'get',
      url: 'http://pc2b39ydf.bkt.clouddn.com/video2.json',
      headers: {
        'Content-Type': 'application/json;charset=utf-8'
      }
    })
  }
}



