const url = {
  WWW_DEV_URL: 'https://www-test.zhangmenkid.com/api',
  WWW_PRO_URL: 'https://www.zhangmenkid.com/api',
}

let wwwURL, wwwOffiURL, bannerGroupID;

if (process.env.NODE_ENV === 'production') {
  wwwURL = url.WWW_PRO_URL;
  bannerGroupID = '5b333dee7c15aa7c5b08f2f0';
}

if (process.env.NODE_ENV === 'development' || process.env.NODE_ENV === 'testing') {
  wwwURL = url.WWW_DEV_URL;
  bannerGroupID = '5b31edd894d289397ea9bcc7';
}

export default {
  wwwURL,
  bannerGroupID,
  nodeHost: '/node-host'
}



