import Vue from 'vue'
import Router from 'vue-router'
import App from '@/App'

const Home = () =>import('@/page/home/home')
const test = () =>import('@/page/test/test')
const ComponentNotFound = () =>import('@/page/404/404')

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: App,
      children: [
        {
          path: '',
          component: Home
        },
        {
          path: '/test',
          component: test
        },
      ]
    },
    {
      path: '*',
      component: ComponentNotFound
    }
  ]
})



