import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './action'
import getters from './getters'

Vue.use(Vuex)

const state = {
  isLoading: false,
  subjectData: [],
  subjectGrade: [],
  subjectToLabel: {}
  // gradeToLabel: {}
}

export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})



